.layout-base, .layout-left-sidebar, .layout-left-full-height-sidebar {
  display: grid;
  width: 100%;
  height: 100vh;
}

.layout-base {
  grid-template-rows: 1fr 40px;
  grid-template-columns: 1fr;
  grid-template-areas: "head head" "main main" "foot foot";
}

.layout-base header {
  grid-area: head;
}

.layout-base > nav {
  grid-area: nav;
}

.layout-base > nav.overflow-y-auto {
  overflow-y: auto;
}

.layout-base > main {
  grid-area: main;
}

.layout-base > main.overflow-y-auto {
  overflow-y: auto;
}

.layout-base > footer {
  grid-area: foot;
}

.layout-left-sidebar {
  grid-template-rows: minmax(56px, auto) 1fr 40px;
  grid-template-columns: 200px 1fr;
  grid-template-areas: "head head" "nav main" "nav foot";
}

.layout-left-sidebar header {
  grid-area: head;
}

.layout-left-sidebar > nav {
  grid-area: nav;
}

.layout-left-sidebar > nav.overflow-y-auto {
  overflow-y: auto;
}

.layout-left-sidebar > main {
  grid-area: main;
}

.layout-left-sidebar > main.overflow-y-auto {
  overflow-y: auto;
}

.layout-left-sidebar > footer {
  grid-area: foot;
}

.layout-left-full-height-sidebar {
  grid-template-rows: minmax(56px, auto) 1fr 40px;
  grid-template-columns: 200px 1fr;
  grid-template-areas: "nav head" "nav main" "nav foot";
}

.layout-left-full-height-sidebar header {
  grid-area: head;
}

.layout-left-full-height-sidebar > nav {
  grid-area: nav;
}

.layout-left-full-height-sidebar > nav.overflow-y-auto {
  overflow-y: auto;
}

.layout-left-full-height-sidebar > main {
  grid-area: main;
}

.layout-left-full-height-sidebar > main.overflow-y-auto {
  overflow-y: auto;
}

.layout-left-full-height-sidebar > footer {
  grid-area: foot;
}

